import React from 'react'
import classNames from 'classnames/bind'
import scss from './header.module.scss'

const cx = classNames.bind(scss)

function Partners() {
  const partners = [
    {
      name: ''
    }, {
      name: ''
    }, {
      name: ''
    }, {
      name: ''
    }, {
      name: ''
    }
  ]
  return (
    <div className={cx('partners')}>
      {partners.map((item, index) => {
        return (
          <a className={cx('partners__item', 'item-' + index)} key={index}>
            <i className={cx('partners__logo', 'logo-' + index)}/>
            <span className={cx('partners__title')}>{item.name}</span>
          </a>
        )
      })}
      <span className={cx('partners__title')}></span>
    </div>
  )
}

export default Partners
