import http from '@/api/http.js'
import { baseGateway, productGateway } from '@/api/api_config.js'

// 
export function createAccountAuto(params, headers) {
  return http.post(`${baseGateway}/customer/createAccountAuto`, params, headers)
}

// 
export function createAccountByMobileNo(params, headers) {
  return http.post(`${baseGateway}/customer/createAccountByMobileNo`, params, headers)
}

// 
export function logout(params = {}, headers) {
  return http.post(`${baseGateway}/customer/logoutV2`, params, headers)
}

// 
export function loginEx(params = {}, headers) {
  return http.post(`${baseGateway}/customer/loginEx`, params, headers)
}

// 
export function modifyTempLoginName(params = {}, headers) {
  return http.post(`${baseGateway}/customer/modifyTempLoginName`, params, headers)
}

// 
export function modifyPwd(params = {}, headers) {
  return http.post(`${baseGateway}/customer/modifyPwd`, params, headers)
}

// 
export function modify(params = {}, headers) {
  return http.post(`${baseGateway}/customer/modify`, params, headers)
}

// 
export function getCustomerInfo(params = {}, headers) {
  return http.post(`${baseGateway}/customer/getByLoginName`, params, headers)
}

// 
export function getBalance(params = {}, headers) {
  return http.post(`${baseGateway}/customer/getBalance`, params, headers)
}

export default {
  createAccountAuto,
  createAccountByMobileNo,
  getCustomerInfo,
  getBalance,
  logout,
  loginEx,
  modifyTempLoginName,
  modifyPwd,
  modify
}
